function fetchMovies() {
  return fetch(
    'https://api.themoviedb.org/3/trending/movie/day?language=en-US&api_key=f71c5261a9fba20ceeb62f41ed4cce9c'
  ).then(resp => {
    if (resp.ok) {
      return resp.json();
    }

    return Promise.reject(new Error('There are no movies'));
  });
}

const api = {
  fetchMovies,
};
export default api;
